<template>
  <div class="post bd-radius">
    <article>
      <h2>{{ post.title }}</h2>

      <figure v-if="post.figure"
        :style="{ 'background-image' : bgVar }"></figure>

      <p>{{ post.summary }}</p>

      <div class="clearfix">
        <!-- <span>by</span> -->
        <img :src="resizeImg | gravatar" alt="">
        <span class="dateline">{{ post.createdAt | ago }}</span>
      </div>
    </article>
  </div>
</template>

<script>
  export default {
		props: ['post'],

    data () {
      return {
        bgVar: 'url("http://uploads.bushangban.com/uploads/images/187eb1f9-d078-433c-99f2-10ff1dc8a3eb-1024x1024.jpg")'
      }
    },
    methods: {
      resizeImg () {
        console.log('obj');
      }
    }
  }
</script>

<style lang="stylus" scoped>
  @import '../assets/stylus/variables.styl'

  div.post
    width 100%
    margin-bottom 15px
    border 1px solid $bdColor
    background white
    box-shadow 0 1px 5px 0 rgba(0,0,0,.05)
    cursor pointer
    article
      padding 10px 20px
      figure
        width 100%
        height 200px
        margin 0
        margin-top 15px
        background-position center
      h2
        margin-top 10px
      p
        margin 10px 0
      div
        color $grayColor
        height 35px
        img
          width 35px
          height 35px
          border-radius 100%
        span
          float left
          line-height 35px
          &.dateline
            float right
</style>



