import {
	RECEIVE_POSTS,
	UPDATE_DRAFT_POST,
	GET_POST
} from '../mutation-types'

// initial state
const state = {
	all: [],
	draft: {
		title: '',
		summary: '',
		content: ''
	},
	currentPostId: '',
}

// mutations
const mutations = {
	[RECEIVE_POSTS] (state, posts) {
    state.all = posts
  },

	[UPDATE_DRAFT_POST] (state, key, value) {
		state.draft[key] = value
	},

	[GET_POST] (state, post) {
		// state.post = post
	}
}

export default {
	state,
	mutations
}



