import Index from 'views/Index'
import CreatePost from 'views/CreatePost'
import ShowPost from 'views/ShowPost'

export default (router) => {
  router.map({
    '/': {
      component: Index
    },
    '/create-post': {
      name: 'createPost',
      component: CreatePost,
    },
		'/p/:id': {
      name: 'showPost',
      component: ShowPost,
    }
  })
}



