<template>
  <div class="create-post container">
    <form action="">
      <input :value="draft.title"
				@input="updateDraftPost('title', $event.target.value)"
				class="title"
				type="text"
				placeholder="标题">

      <input :value="draft.summary"
				@input="updateDraftPost('summary', $event.target.value)"
				class="summary"
				type="text"
				placeholder="简介">

      <textarea :value="draft.content"
				@input="updateDraftPost('content', $event.target.value)"
      	placeholder="内容, markdown 语法"></textarea>

      <a @click="createPost"
        class="btn btn-primary btn-radius">保存</a>

    </form>
  </div>

</template>

<script>
  import {
		createPost,
		updateDraftPost
	} from 'src/vuex/actions'


  export default {
		vuex: {
			actions: {
				updateDraftPost,
				createPost,
			},

			getters: {
				draft: ({ posts }) => posts.draft
			}
		},

  }
</script>

<style lang="stylus">
  @import '../assets/stylus/variables.styl'

  div.create-post
    padding-top 20px

    input,textarea
      background $bgColor
      border 0
      width 100%

    input
      margin 10px 0
      line-height 1.6
      &.title
        font-size 30px
      &.summary
        font-size 20px

    textarea
      height 400px

</style>



