import api from 'src/api'
import {
	RECEIVE_POSTS,
	UPDATE_DRAFT_POST,
	CREATE_POSTS,
	GET_POST
} from './mutation-types'

export const bindPosts = ({ dispatch }) => {
	api.Post.bindAll(posts => {
		dispatch(RECEIVE_POSTS, posts)
	})
}

export const updateDraftPost = ({dispatch}, key, value) => {
	dispatch(UPDATE_DRAFT_POST, key, value)
}

export const createPost = ({dispatch, state}) => {
	api.Post.create(state.posts.draft)
		.then(post => {
			dispatch(CREATE_POSTS, post)
		})
}

export const getPost = ({ dispatch, state }, id) => {
	if (state.posts.all[id])

	api.Post.get(id)
		.then(post => {
			dispatch(GET_POST, post)
		})
}



